<script>
import L from 'leaflet';
import cityName from './assets/cityData.json';

const getMaskCountClass = (count) => {
  if (count <= 0) {
    return 'soldout';
  }
  return '';
};

let osmMap = {};

const iconsConfig = {
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41],
};
const markerIcons = {
  blue: new L.Icon({
    iconUrl: 'https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-blue.png',
    ...iconsConfig,
  }),
  grey: new L.Icon({
    iconUrl: 'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-grey.png',
    ...iconsConfig,
  }),
};

// addPopup(pharmacy) {
//     return `<h3 class="popup_title">${pharmacy.properties.name}</h3>
//     <div class="popup_link"><a href="tel:+886-${pharmacy.phone.substring(1).replace(' ', '')}">
//     ${pharmacy.properties.phone}</a></div>
//    <div class="popup_link">
//       <a target="_blank" href="https://www.google.com.tw/maps/place/${pharmacy.properties.address}">
//       ${pharmacy.properties.address}</a>
//     </div>
//     <div class="mask_quantity">
//       <div class="">成人：${pharmacy.properties.mask_adult}</div>
//       <div class="soldout">兒童：${pharmacy.properties.mask_child}</div>
//     </div>
//     <div>{{pharmacy.properties.updated}} 更新</div>
//   `;
//   }

const markerModus = {
  addMarker(lat, lng, icon = markerIcons.blue, pharmacy, isOpenPopUp = false) {
    const markers = new L.MarkerClusterGroup();
    const layer = L.marker([lat, lng], { icon })
      .addTo(osmMap).bindPopup(`<h3 class="popup_title">${pharmacy.name}</h3>
      <div class="popup_link"><a href="tel:+886-${pharmacy.phone.substring(1).replace(' ', '')}">
        ${pharmacy.phone}</a></div>
      <div class="popup_link">
        <a target="_blank" href="https://www.google.com.tw/maps/place/${pharmacy.address}">
          ${pharmacy.address}</a>
      </div>
      <div class="mask_quantity">
      <div class="${getMaskCountClass(pharmacy.mask_adult)}">
        成人：${pharmacy.mask_adult}</div>
      <div class="${getMaskCountClass(pharmacy.mask_child)}">
        兒童：${pharmacy.mask_child}</div>
      </div>
      <div>${pharmacy.updated} 更新</div>`);
    markers.addLayer(layer);
    osmMap.addLayer(markers);
    if (isOpenPopUp === true) {
      layer.openPopup();
    }
  },
  removeAllMarkers() {
    osmMap.eachLayer((layer) => {
      if (layer instanceof L.Marker) {
        osmMap.removeLayer(layer);
      }
    });
  },
  moveTo(pharmacy) {
    const [lng, lat] = pharmacy.geometry.coordinates;
    osmMap.panTo(new L.LatLng(lat, lng));
  },
};

export default {
  name: 'App',
  data() {
    return {
      data: [],
      cityName,
      osmMap: {},
      selected: {
        city: '台北市',
        area: '大安區',
      },
      center: [25.030459, 121.539662],
      zoom: 20,
    };
  },
  methods: {
    setMarkers() {
      this.pharmacyList.forEach((pharmacy) => {
        const { properties, geometry } = pharmacy;
        const [lng, lat] = geometry.coordinates;
        const { mask_adult: maskAdultCount, mask_child: maskChildCount } = properties;
        const icon = maskAdultCount || maskChildCount ? markerIcons.blue : markerIcons.grey;
        markerModus.addMarker(lat, lng, icon, properties);
      });
    },
    updateMarker() {
      markerModus.removeAllMarkers();
      this.setMarkers();
      if (this.pharmacyList.length !== 0) {
        this.focus(this.pharmacyList[0]);
      }
    },
    openPopUp(pharmacy) {
      const { properties, geometry } = pharmacy;
      const [lng, lat] = geometry.coordinates;
      const { mask_adult: maskAdultCount, mask_child: maskChildCount } = properties;
      const icon = maskAdultCount || maskChildCount ? markerIcons.blue : markerIcons.grey;
      markerModus.addMarker(lat, lng, icon, properties, true);
    },
    focus(pharmacy) {
      markerModus.moveTo(pharmacy);
      this.openPopUp(pharmacy);
    },
  },
  computed: {
    areaList() {
      return this.cityName
        .find((city) => city.CityName === this.selected.city).AreaList;
    },
    pharmacyList() {
      return this.data.filter((item) => {
        if (this.selected.area === 'null') {
          return item.properties.county === this.selected.city;
        }
        return item.properties.county === this.selected.city
        && item.properties.town === this.selected.area;
      });
    },
  },
  mounted() {
    osmMap = L.map('maskMap', {
      center: this.center,
      zoom: 15,
      zoomControl: false,
    });
    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: '<a target="_blank" href="https://www.openstreetmap.org/">© OpenStreetMap</a>',
      maxZoom: 20,
    }).addTo(osmMap);
    L.control.zoom({
      position: 'bottomright',
    }).addTo(osmMap);
  },
  created() {
    this.selected.city = this.cityName[0].CityName;
    const url = 'https://raw.githubusercontent.com/kiang/pharmacies/master/json/points.json';
    this.$http.get(url)
      .then((res) => {
        this.data = res.data.features;
        this.updateMarker();
      });
  },
};
</script>

